
header {
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}

header .container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

header div {
  margin: 10px 0;
}

.header-left {
  background: #eee;
  padding: 20px;
  margin-right: 20px;
}

.header-left form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header-left form input {
  width: 100%;
}

.header-left .btn-danger {
  margin-left: 10px;
}

label {
  padding-right: 5px;
  -webkit-align-self: center;
          align-self: center;
  margin-bottom: 0;
}
.header-left form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: inherit;
          align-items: inherit;
}

@media screen and (min-width: 768px) {
  header .container {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}
.clear-all-expenses-btns {
  text-align: center;
  margin: 20px 0;
}

.clear-all-expenses-btns button {
  margin: 0 10px;
}

#expenses-table {
  margin: 0 auto;
}

#expenses-table td {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

#expenses-table td:nth-child(3),
#expenses-table td:nth-child(4) {
  background: #DC3545;
}

#expenses-table tr {
  display: -webkit-flex;
  display: flex;
}

.expense-name {
  -webkit-flex: 2 1;
          flex: 2 1;
}

#expenses-table .expense-remove-btn {
  position: relative;
}

#expenses-table .expense-remove-btn  button {
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}


@media screen and (min-width: 768px) {
  #expenses {
    position: relative;
  }

  .clear-all-expenses-btns {
    position: absolute;
    right: -43px;
    top: -50px;
  }
}
footer {
  margin-top: 140px;
}

p {
  text-align: center;
}

footer a {
  color: #212529;
  text-decoration: underline;
}
.cost-input,
.budget-input {
  width: 60px;
}

button.btn {
  border-radius: 0;
}

.budget-display {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.reset-budget-btn {
  margin-left: 5px;
}

input {
  border: none;
  border-bottom: 1px solid #ddd;
  margin: 0 10px;
  padding: 5px;
}

label { margin: 0; }

.table { width: 100%; max-width: 600px; margin: 0 auto; }

.table td {
  padding: 0;
  vertical-align: middle;
}

td.expense-name,
td.expense-cost {
  padding: 0 5px;
}

td.expense-edit-btn { width: 60px }
td.expense-remove-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex : 1 1;
          flex : 1 1;
}

table .btn-danger,
table .btn-secondary {
  width: 100%;
}

.btn {
  border: 0; border-radius: 0;
  border: 0;
  border-radius: 0;
}

#empty-expense-list-msg {
  font-size: 1.4em;
  color: #920303;
  font-style: italic;
}

.error-msg,
.danger {
  color: #920303;
}
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Patrick Hand', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


